/* Header Styling */
.App-header {
  background-color: rgba(40, 44, 52, 0.85); /* Slight transparency */
  padding: 20px;
  color: #ffffff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

/* Main Content */
main {
  max-width: 800px;
  margin: 40px auto;
  padding: 20px;
  background-color: rgb(255, 255, 255);
  border-radius: 16px;
  /*backdrop-filter: blur(8px);*/
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);
}

/* Dropzone Styling */
.dropzone {
  border: 2px dashed #6a6f72;
  border-radius: 12px;
  padding: 40px;
  margin: 20px 0;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
}

.dropzone:hover {
  background-color: rgba(0, 0, 0, 0.2);
}

/* Preview Section */
.preview img {
  max-width: 300px;
  border-radius: 12px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
}

/* Result Section */
.results {
  margin: 20px 0;
  padding: 20px;
  border-radius: 12px;
  background-color: rgba(0, 0, 0, 0.1);
}

.positive {
  color: #ff6b6b; /* Soft Red */
}

.negative {
  color: #48c774; /* Soft Green */
}

.error {
  color: #ff6b6b;
}

/* Footer */
footer {
  margin-top: 40px;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.2);
  font-size: 14px;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}